.icon {
  background-position: center;
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
  display: block;
}

.icon--logo {
  background-image: url("logo-mobile.9273f922.svg");
  width: 161px;
  margin-right: 67px;
  transition: transform .3s;
}

.icon--logo:hover {
  transform: scale(1.2);
}

@media (width >= 640px) {
  .icon--logo {
    margin-right: 0;
  }
}

.icon--menu {
  background-image: url("menu.6f7db571.svg");
  width: 24px;
  height: 16px;
  transition: transform .3s;
}

.icon--menu:hover {
  transform: scale(1.5);
}

@media (width >= 1280px) {
  .icon--menu {
    background-image: none;
  }
}

.icon--close {
  background-image: url("close.2cc993a8.svg");
  background-size: contain;
  width: 16px;
  transition: transform .3s;
}

.icon--close:hover {
  transform: scale(1.5);
}

.icon--futuristicDesign {
  background-image: url("vector.e2371a03.svg");
