.user__card {
  text-align: center;

  &__desc {
    font-family: inherit;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 30px;
    color: $c-dark-gray;
    padding-top: 50px;

    @include on-tablet {
      font-size: 2cap;
      line-height: 36px;
    }
  }

  &__avatar {
    width: 62px;
    height: 62px;
    border-radius: 50%;
    margin-bottom: 10px;
  }

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 5px;
    color: $c-dark-gray;
  }

  &__position {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: $c-gray;
  }
}
